<script>

    import Simulation from './Simulation.svelte'
	import AddLayer from './AddLayer.svelte';
    import {Xlabel, Ylabel} from './store'

    let layersObj = [];

    let postData;
    let fmin;
    let fmax;

    let xLabel;
    let yLabel;

    async function PostData(data, fmin, fmax){
        const response = await fetch('https://panelsim.herokuapp.com/simulation/?f_init='+fmin+'&f_final='+fmax, {
            method: 'POST',
            mode: 'cors',
            cache: 'no-cache',
            credentials: 'same-origin',
            mode: 'cors',
            headers: {
                'Content-type': 'application/json',
                'Access-Control-Allow-Origin':'*',
                'Access-Control-Allow-Methods': 'GET',
                'Access-Control-Allow-Headers':'*',
                'cache-control': 'no-cache',
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            redirect: 'follow',
            body: `{"data": ${JSON.stringify(data)}}`
        });
        console.log('Fetch to: ', 'https://panelsim.herokuapp.com/simulation/?f_init='+fmin+'&f_final='+fmax)
        return response.json();
    }

    function addLayerProps(id, area, thickness, material){
        let layer = {id: id, area: area, thickness: thickness, material: material};
        layersObj.push(layer);

        console.log(layersObj)
    }

    function DeleteById(){
        console.log(layersObj)
        layersObj.pop()
    }

    let layers = 1;

    function AddLayerFunction(id, area, thickness, material){
        if (id != 3){
            layers += 1;
            addLayerProps(id, area, thickness, material)
        } else{
            addLayerProps(id, area, thickness, material)
        }

    }

    function AddZeroLayer(){
        layers += 1;
    }

    function DeleteLayerFunction(Id){
        layers = layers - 1;
        DeleteById(Id);
        console.log(layers);
    }

    function Simulate(){
        if (layersObj.length > 0){
            PostData(layersObj, fmin, fmax)
            .then(data => {
                postData = data.response;
                const fArray = makeArr(fmin, fmax, 1000);
                const dataC = generateConcat(postData, fArray);

                xLabel = fArray;
                yLabel = postData;
                updateStore(xLabel, yLabel)
            });

        } else{
            console.log(layersObj.length)
        }
    }

    function makeArr(startValue, stopValue, cardinality) {
        var arr = [];
        var step = (stopValue - startValue) / (cardinality - 1);
        for (var i = 0; i < cardinality; i++) {
            arr.push(startValue + (step * i));
        }
        return arr;
    }

    function generateConcat(post, frec){
        let concat = []
        for (var i = 0; i < 1000; i++){

            concat.push({x: frec[i], y: post[i]})
        }

        return concat
    }

    const updateStore = (x, y) => {
        $Xlabel = x;
        $Ylabel = y;

        console.log($Xlabel, $Ylabel)
    }

</script>

<div class='wrapper'>
    <h4>Añadir capas</h4>

    {#if layers == 0}

        <AddLayer AddZero={AddZeroLayer} NLayers={0} Id={1} AddLayerFunction={AddLayerFunction} DeleteLayerFunction={DeleteLayerFunction}></AddLayer>

    {:else}

    {#each Array(layers) as _, i}
        <AddLayer AddZero={AddZeroLayer} NLayers={layers} Id={i + 1} AddLayerFunction={AddLayerFunction} DeleteLayerFunction={DeleteLayerFunction}></AddLayer>
    {/each}

    {/if}

    <input input min = "0" placeholder="Frecuencia mínima [Hz]" type="number" bind:value={fmin}/>
    <input input min = "0" placeholder="Frecuencia máxima [Hz]" type="number" bind:value={fmax}/>
    <button on:click={Simulate}>Simular</button>

</div>



<style>
    div.wrapper {
        width: 100%;
        position: fixed;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 5px;
        flex-direction: column;
    }

    button{
        background-color: white;
        color: #0EB1D2;
        font-weight: bold;
        width: 250px;
