<script>

import { onMount } from 'svelte';

    const material_api_url = 'https://panelsim.herokuapp.com/materials/';
    let apiData;

    export let AddLayerFunction;
    export let DeleteLayerFunction;
    export let Id;
    export let NLayers;
    export let AddZero;

    let selected;
    let area;
    let thickness;

    let switcherLastedButton = true;

    function submit(id, area, thickness, material){
        AddLayerFunction(id, area, thickness, material);
        switcherLastedButton = !switcherLastedButton;
    }

    function deleteASubmit(id){
        console.log("Delete", id)
        DeleteLayerFunction(id)
    }

    function submitZero(){
        AddZero();
    }

    onMount(async () => {
    fetch(material_api_url, {
        method: 'GET',
        mode: 'cors',
        cache: 'no-cache',
        credentials: 'same-origin',
        headers: {
            'Content-type': 'application/json',
            'Access-Control-Allow-Origin':'*',
            'Access-Control-Allow-Methods': 'GET',
            'Access-Control-Allow-Headers':'*',
            'cache-control': 'no-cache'
        }
    })
    .then(response => response.json())
    .then(data => {
        apiData = data;
    }).catch(error => {
        console.log(error);
        return [];
    })
    });

</script>

<div>

    {#if apiData}

    {#if NLayers != 0}
        <input min = "0" placeholder="Área" type="number" bind:value={area}/>
        <input min = "0" placeholder="Espesor" type="number" bind:value={thickness}/>
        <form on:submit|preventDefault={submit}>
            <select bind:value={selected} name="select_material">
                {#each apiData['materials'] as material}
                    <option value={material.name}>{material.name}</option>
                {/each}
            </select>
        </form>

    {/if}

    {#if Id == 1}
        {#if NLayers == 1}

            <button on:click={()=> submit(Id, area, thickness, selected)}>Añadir capa</button>
            <button on:click={()=> deleteASubmit(Id)}>Eliminar capa</button>

        {:else if NLayers == 0}

            <button on:click={submitZero}>Añadir capa</button>

        {:else if NLayers == 2}

            <button on:click={()=> deleteASubmit(Id)}>Eliminar capa</button>

        {:else}

            <button on:click={()=> deleteASubmit(Id)}>Eliminar capa</button>

        {/if}

    {:else if Id == 2}

        {#if NLayers == 2}

            <button on:click={()=> submit(Id, area, thickness, selected)}>Añadir capa</button>
            <button on:click={()=> deleteASubmit(Id)}>Eliminar capa</button>

        {:else}

            <button on:click={()=> deleteASubmit(Id)}>Eliminar capa</button>

        {/if}

    {:else}

        {#if switcherLastedButton == true}
            <button on:click={()=> submit(Id, area, thickness, selected)}>Añadir capa</button>
        {/if}

        <button on:click={()=> deleteASubmit(Id)}>Eliminar capa</button>

    {/if}
    {:else}
        <p>Cargando ...</p>
    {/if}
</div>

<style>
    div {
        display: flex;
        flex-direction: row;
        width: 100%;
        align-items: center;
        justify-content: center;
    }

    form{
        display: flex;
        flex-direction: column;
    }

</style>